body {
    padding-top: 3rem;
  }
  .starter-template {
    padding: 3rem 1.5rem;
    text-align: center;
  }

table tr{
    cursor: move;
}

.title{
    text-transform: uppercase;
}

.done{
    text-decoration: line-through;
}

.undone{
    text-decoration: none;
}

.taskInput{
    width: 150px;
}

#todoTable{
    margin-top: 30px;
}

#todoTable td{
    padding-right: 15px;
}

.width-100{
    width: 100%;
    text-align: left;
}

.read-only-input{
    border: 0px;
}


